*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 62.5%;
}
.navbar-brand {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-weight: 600;
  font-size: 3.0rem;
}

.nav-link {
  font-weight: 600;
  font-size: 2.0rem;
  margin-left: 4rem;
}

.nav-link:hover {
  font-weight: 800;
  text-decoration: underline blue;
}

.header {
  font-size: 5rem;
  font-family: 'Rubik', sans-serif;
  color: crimson;
}

#img1 {
  margin-left: 100rem;
  left: 10rem;
  margin-top: 10rem;
  width: 40rem;
}

.header2 {
  font-size: 4.5rem;
  font-family: 'Rubik', sans-serif;
  position: absolute;
  left: 25rem;
  top: 25rem;
}

.para {
  font-size: 2.0rem;
  font-family: 'Rubik', sans-serif;
  color: black;
  position: absolute;
  left: 25rem;
  top: 32rem;
}

.btn2 {
  position: absolute;
  left: 25rem;
  top: 50rem;
  width: 15rem;
  height: 4rem;
  background-color: blue;
  color: beige;
  border-radius: 3rem;
}

#img2 {
margin-left: 20rem;
  left: 25rem;
  margin-top: 30rem;
  width: 40rem;
  height: 30rem;
}

.header3 {
  font-size: 4.5rem;
  font-family: 'Rubik', sans-serif;
  position: absolute;
  left: 85rem;
  top: 90rem;
}

.para2 {
  font-size: 2rem;
  font-family: 'Rubik', sans-serif;
  color: black;
  position: absolute;
  left: 85rem;
  top: 97rem;
}

.btn3 {
  position: absolute;
  left: 84rem;
  top: 112rem;
  width: 15rem;
  height: 4rem;
  background-color: blue;
  color: beige;
  border-radius: 3rem;
}
.center{
  background: linear-gradient(120deg,#2980b9,#8a44ad);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 50rem;
  border-radius: 1rem;
}
.center h1{
  text-align: center;
  padding: 0 0 20px;
}

@media (max-width: 1668px) {
  html{
    font-size: 55%;
  }
}
@media (max-width: 988px) {
  html{
    font-size: 45%;
  }
}
@media (min-width: 360px)and(max-width: 740px) {
  html{
    font-size: 35%;
  }
}
@media (min-width: 360px)and(max-width: 740px) {
  html{
    font-size: 25%;
  }
}